@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
.topbar{-webkit-backface-visibility:hidden;backface-visibility:hidden;background-color:#16202B;left:0;padding:8px 16px;position:fixed;top:0;width:100%;z-index:9000}.topbar .container{display:flex;justify-content:space-between}.topbar__logo{display:inline-block;height:40px}.topbar__logo svg{height:100%}.topbar__group{display:flex;justify-content:flex-end}.topbar__icon,.topbar__icon a{align-items:center;color:#858D93;display:flex;flex-direction:column;font-size:1.6rem;font-weight:normal;justify-content:center;margin:auto 0.5rem;transition:all 0.2s ease-in-out}.topbar__icon span,.topbar__icon a span{display:none}.topbar__icon a:hover{color:white}@media screen and (min-width: 728px){.topbar__icon,.topbar__icon a{margin:auto 0.5rem}.topbar__icon span,.topbar__icon a span{display:inline-block;font-size:1.1rem}}

.footer{-webkit-backface-visibility:hidden;backface-visibility:hidden;background-color:#16202B;left:0;padding:8px 16px;position:fixed;bottom:0;width:100%;z-index:9000}.footer .container{display:flex;justify-content:space-between}.footer a{align-items:center;color:#858D93;display:flex;flex-direction:column;font-size:1.4rem;font-weight:normal;justify-content:center;margin:auto 0.5rem;transition:all 0.2s ease-in-out}.footer a:hover{color:white}.footer span{color:#858D93;font-size:1.4rem;align-items:center}@media screen and (max-width: 728px){.footer a{display:inline-block;font-size:1.1rem}.footer span{display:inline-block;font-size:1.1rem}}

.story{background-color:#08111E;display:flex;flex-direction:column;height:100vh;left:0;position:fixed;top:0;width:100%;z-index:9999}.story__header{overflow:hidden;padding:16px;align-items:center;display:flex;justify-content:space-between}.story__progress{background-color:rgba(255,255,255,0.05);border-radius:4px;height:4px;overflow:hidden;width:100%;padding:1px}.story__progress__elapsed{transition:width ease-in-out 0.05s;width:0;height:1px;background:linear-gradient(to right, #FF991C 0%, #D51589 100%)}.story__close{font-size:1.6rem;color:#858d93}.story__header .user{display:flex;align-items:center}.story__header .user__thumb{height:40px;margin-right:8px;border:1px solid #FF991C;width:40px}.story__header .user__name{font-size:1.2rem;font-weight:bold;color:white;text-decoration:none}.story__video__wrapper{background-color:black;height:calc(100vh - 104px);overflow:hidden;border:5px solid #16202b;padding:0;border-radius:30px;position:relative;width:100%;margin:16px 0}.video-player{position:absolute;width:100%;max-width:100%;height:100%}

.stories{background-color:#16202B;overflow-x:hidden;overflow-y:auto;padding:16px;width:100%}.stories .container{align-items:center;display:flex;justify-content:flex-start;margin:0 auto}.stories .user__thumb{align-items:center;display:flex;flex-direction:column;border:none;height:72px;justify-content:center;margin:0 5px;padding:0;width:72px}.stories .user__thumb__wrapper{height:65px;width:65px}

.loading{align-items:center;color:#16202B;display:flex;flex-direction:column;height:100px;justify-content:center;left:50%;position:absolute;top:50%;transform:translate(-50%);width:100px;font-weight:bold;font-size:1.3rem}.preloader{position:relative;width:65px;margin-bottom:20px}.preloader span{position:absolute;display:block;bottom:0;width:9px;height:5px;border-radius:5px;background:#16202B;-webkit-animation:preloader 2s infinite ease-in-out;animation:preloader 2s infinite ease-in-out}.preloader span:nth-child(2){left:11px;-webkit-animation-delay:200ms;animation-delay:200ms}.preloader span:nth-child(3){left:22px;-webkit-animation-delay:400ms;animation-delay:400ms}.preloader span:nth-child(4){left:33px;-webkit-animation-delay:600ms;animation-delay:600ms}.preloader span:nth-child(5){left:44px;-webkit-animation-delay:800ms;animation-delay:800ms}.preloader span:nth-child(6){left:55px;-webkit-animation-delay:1000ms;animation-delay:1000ms}@-webkit-keyframes preloader{0%{height:5px;transform:translateY(0);background:#f88738}25%{height:30px;transform:translateY(15px);background:#DC327B}50%,100%{height:5px;transform:translateY(0);background:#16202B}}@keyframes preloader{0%{height:5px;transform:translateY(0);background:#f88738}25%{height:30px;transform:translateY(15px);background:#DC327B}50%,100%{height:5px;transform:translateY(0);background:#16202B}}

.post{background-color:#08111E;display:flex;flex-direction:column;margin-bottom:30px;overflow:hidden}.post__header{align-items:center;display:flex;justify-content:space-between;padding:8px}.post__header .user{display:flex;align-items:center}.post__header .user__thumb{height:40px;margin-right:8px;border:1px solid #FF991C;width:40px}.follow-btn{color:#858D93;font-weight:600;font-size:1.2rem}.follow-btn.is-following{color:#ED2168}.post__header .user__name{font-size:1.2rem;font-weight:bold;color:white;text-decoration:none}.post__context{font-size:1.4rem}.post__figure{border-radius:30px;height:auto;margin:0;overflow:hidden;width:100%}.post__figure img{width:100%}.post__controls{width:100%;display:flex;justify-content:flex-start;align-items:center;padding:4px 0}.post__control{color:#858D93;font-size:2.4rem;margin-right:5px;position:relative}.fas.fa-heart{color:#ED2168}.post__status{display:flex;padding:0;line-height:1.4em}.post__status span{color:#858D93}.post__status .user{display:flex;justify-content:flex-start;align-items:center}.post__status .user span{font-size:1.2rem}.post__status .user__thumb{height:30px;width:30px;margin-right:8px}

.feed{display:flex;flex-direction:column;padding:16px;position:relative;width:100%}@media screen and (min-width: 728px){.feed{padding:16px 0}}

.users-list{display:flex;flex-direction:row;flex-wrap:wrap;padding:8px;position:relative;width:100%;justify-content:center;align-items:center}.users-list .post{align-items:center;background-color:#16202b;border-radius:6px;display:flex;flex-direction:column;justify-content:center;margin:6px auto;overflow:hidden;padding:0;transition:all ease-in-out .2s;width:calc((100% / 2) - 12px)}.users-list .post:hover{background-color:rgba(22,32,43,0.6)}.users-list .post:hover .post__header .user__name{color:white}.users-list .post:hover .user__thumb{background:linear-gradient(45deg, #D51589 0%, #FF991C 100%)}@media screen and (min-width: 728px){.users-list .users-list{padding:40px 16px}.users-list .post{width:calc((100% / 3) - 12px)}}.users-list .post__header,.users-list .post__header .user{align-items:center;display:flex;flex-direction:column;justify-content:center;padding:0;width:100%}.users-list .post__header .user{padding:15px}.users-list .post__header .user__name{padding:15px 0;font-size:1.4rem;color:#858d93;transition:all ease-in-out .2s}.users-list .user__thumb{background:linear-gradient(45deg, #08111e 0%, #08111e 100%);border:2px solid transparent;height:100px;margin:0;transition:all ease-in-out .2s;width:100px}@media screen and (min-width: 728px){.users-list{padding:16px 0}}

.profile{background-color:#16202B;display:flex;flex-direction:column;position:relative}.profile-data{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:20px}.profile-data .user{display:flex;flex-direction:column;justify-content:center;align-items:center}.profile-data .user__thumb{background:linear-gradient(45deg, #D51589 0%, #FF991C 100%);border:2px solid transparent;height:150px;width:150px;margin:0}.profile-data .user__name{display:flex;align-items:center;padding:0;font-size:2rem;line-height:1;margin-bottom:0;font-weight:bold;transition:all ease-in-out .2s;color:white}.profile-data .user__name span{color:#858d93;font-weight:normal;font-size:1.4rem}.profile-data .user__name span:before{content:"-";margin:0 10px}@media screen and (min-width: 728px){.profile-data{padding:40px 16px 20px}.profile-data .user__thumb{height:200px;width:200px}}

.user-posts{display:flex;flex-direction:row;flex-wrap:wrap;padding:16px;position:relative;justify-content:space-between}.user-posts .post{margin:6px;width:calc((100% / 2) - 12px)}.user-posts .post .post__figure{border-radius:10px}.no-posts{align-items:center;display:flex;flex-direction:column;font-size:1.4rem;justify-content:center;width:100%;padding:40px}.no-posts__content{color:#858d93}.no-posts__emoji{font-size:5rem}@media screen and (min-width: 728px){.user-posts{padding:20px 16px 40px;justify-content:flex-start}.user-posts .post{width:calc((100% / 3) - 12px)}}

.success-message{align-items:center;background-color:#4caf50;border-radius:30px;display:flex;flex-direction:column;height:300px;justify-content:center;left:50%;margin:auto;max-width:400px;overflow:hidden;padding:20px;position:fixed;text-align:center;top:50%;transform:translate(-50%, -50%);width:80%;z-index:2}.success-message__title{font-size:2.4rem;line-height:1}.success-message__content{font-size:1.4rem;line-height:1}.success-message__content a{color:#08111e;text-decoration:underline}.successAnimationCircle{stroke-dasharray:151px;stroke:#FFF}.successAnimationCheck{stroke-dasharray:36px 36px;stroke:#FFF}.successAnimationResult{fill:#FFF;opacity:0}.successAnimation.animated{-webkit-animation:1s ease-out 0s 1 both scaleAnimation;animation:1s ease-out 0s 1 both scaleAnimation}.successAnimation.animated .successAnimationCircle{-webkit-animation:1s cubic-bezier(0.77, 0, 0.175, 1) 0s 1 both drawCircle,0.3s linear 0.9s 1 both fadeOut;animation:1s cubic-bezier(0.77, 0, 0.175, 1) 0s 1 both drawCircle,0.3s linear 0.9s 1 both fadeOut}.successAnimation.animated .successAnimationCheck{-webkit-animation:1s cubic-bezier(0.77, 0, 0.175, 1) 0s 1 both drawCheck,0.3s linear 0.9s 1 both fadeOut;animation:1s cubic-bezier(0.77, 0, 0.175, 1) 0s 1 both drawCheck,0.3s linear 0.9s 1 both fadeOut}.successAnimation.animated .successAnimationResult{-webkit-animation:0.3s linear 0.9s both fadeIn;animation:0.3s linear 0.9s both fadeIn}@-webkit-keyframes scaleAnimation{0%{opacity:0;transform:scale(1.5)}100%{opacity:1;transform:scale(1)}}@keyframes scaleAnimation{0%{opacity:0;transform:scale(1.5)}100%{opacity:1;transform:scale(1)}}@-webkit-keyframes drawCircle{0%{stroke-dashoffset:151px}100%{stroke-dashoffset:0}}@keyframes drawCircle{0%{stroke-dashoffset:151px}100%{stroke-dashoffset:0}}@-webkit-keyframes drawCheck{0%{stroke-dashoffset:36px}100%{stroke-dashoffset:0}}@keyframes drawCheck{0%{stroke-dashoffset:36px}100%{stroke-dashoffset:0}}@-webkit-keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

.post__form{display:flex;flex-direction:column;justify-content:center;padding:16px}@media screen and (min-width: 728px){.post__form{padding:40px}}.post__form .post__form__wrapper{width:100%;margin:0 auto;display:flex;flex-direction:column;justify-content:center}@media screen and (min-width: 728px){.post__form .post__form__wrapper{width:80%}}.post__form .post__form__wrapper button{background:linear-gradient(45deg, #D51589 0%, #FF991C 100%);color:white;font-size:2rem;font-weight:bold;margin:0 auto;padding:10px 20px;border-radius:5px}.post__form label{display:block;color:#858D93;font-size:1.2rem;font-weight:normal;margin-bottom:5px}.post__form input{background-color:rgba(255,255,255,0.02);border-radius:5px;border:1px solid #858D93;color:white;display:block;font-size:16px;font-weight:bold;margin-bottom:20px;outline:none;padding:8px;width:100%;transition:all ease-in-out .2s}.post__form input::-webkit-input-placeholder{font-weight:normal}.post__form input:-ms-input-placeholder{font-weight:normal}.post__form input::-ms-input-placeholder{font-weight:normal}.post__form input::placeholder{font-weight:normal}.post__form input:focus{background-color:rgba(255,255,255,0.1);border:1px solid #F06B41}

*,*:after,*:before{box-sizing:border-box}html{font-size:10px}body{padding-top:56px;padding-bottom:30px;font-family:'Open Sans', sans-serif;background-color:#08111E;color:#fff}a{text-decoration:none;color:#fff;font-weight:bold}button{background-color:transparent;border:none;box-shadow:none;outline:none;-webkit-tap-highlight-color:rgba(0,0,0,0)}.container{margin:0 auto;position:relative;width:100%}@media screen and (min-width: 728px){.container{width:600px}}.user__thumb{display:flex;border-radius:100%;overflow:hidden}.user__thumb__wrapper{border-radius:100%;border:2px solid #16202B;display:inline-block;overflow:hidden}.user__thumb img{height:100%;width:100%}.user__thumb--hasNew{background:linear-gradient(45deg, #D51589 0%, #FF991C 100%)}

